
@import "../../scss/_global.scss";

#modal-call {
  @extend %modal;
  max-width: 865px;
  width: 100%;
  padding: 60px 40px 30px;
  @media (max-width: 576px) {
    padding: 30px 10px;
    border-radius: 16px;
  }

  h3 {
    margin-bottom: 55px;

    @media (max-width: 992px) {
      margin-bottom: 30px;
    }

    @media (max-width: 576px) {
      margin-bottom: 15px;
    }
  }

  .decr {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    color: #707070;

    @media (max-width: 576px) {
      font-size: 12px;
      line-height: 14px;
    }
  }

  .button {
    @extend %button;
    margin: 40px auto 44px;

    @media (max-width: 576px) {
      margin-top: 30px;
      margin-bottom: 24px;
      width: 145px;
    }
  }
  .user-info {
    display: flex;
    gap: 14px;
    flex-direction: column;
    max-width: 575px;
    margin: 0 auto;
    @media (max-width: 576px) {
      gap: 10px;
      max-width: 330px;
    }
  }
}
